#progress {
			appearance: none;
			-webkit-appearance: none;
			width: 100%;
			height: 7px;
			background: rgba(163, 162, 164, 0.4);
			border-radius: 4px;
			margin: 32px 0 24px;
			cursor: pointer;
		}

		#progress::-webkit-slider-thumb {
			appearance: none;
			-webkit-appearance: none;
			background: rgba(89, 26, 151, 0.9);
			width: 16px;
			aspect-ratio: 1/1;
			border-radius: 50%;
			outline: 4px solid $primary-clr;
			box-shadow: 0 6px 10px rgba(5, 36, 28, 0.3);
		}